import { createPublicKey } from "node:crypto";
import type {
  SessionResource,
  SignInResource,
  UserResource,
} from "@clerk/types";
import { type JWTPayload, jwtVerify } from "jose";
import open from "open";
import { err, ok, type Result } from "@/utils/result";
import { delay } from "@/utils/utils";
import { version } from "../../package.json";
import { clerkClient } from "./clerkClient";
import { createLocalhostAuthServer } from "./localhostServer";
import {
  clearStoredAuth,
  createSubscriptionTokenCache,
  createTokenCache,
} from "./tokenCache";

/**
 * Wrapper around Clerk Auth + Tonk Backend
 */

const TONK_BASE_URL = process.env.TONK_BASE_URL || "http://localhost:5173"; // @dev: localhost to enable vite dev on OSX

const TONK_AUTH_URL = `${TONK_BASE_URL}/dashboard/auth-cli`;
const EXPECTED_ISSUER = `${TONK_BASE_URL}`;
const TOKEN_SUBSCRIPTION_URL = `${TONK_BASE_URL}/api/v1/cli-subscription`;

if (!process.env.TONK_PUBLIC_KEY) {
  throw new Error("TONK_PUBLIC_KEY environment variable not set");
}

const publicKey = createPublicKey(
  Buffer.from(process.env.TONK_PUBLIC_KEY, "base64").toString("utf-8"),
);

type CliTokenPayload = JWTPayload & {
  subscription_active: boolean;
  plan: string;
};

type SubscriptionStatus = {
  active: boolean;
  data: CliTokenPayload | null;
};

/**
 * Configuration options for TonkAuth client
 *
 * @example
 * ```typescript
 * const options: TonkAuthOptions = {
 *   // Called when subscription expires or becomes inactive
 *   onSubscriptionDisabled: () => {
 *     console.log('‚ö†Ô∏è Your subscription has expired!');
 *     process.exit(1);
 *   },
 *
 *   // Check subscription every 30 seconds instead of default 1 minute
 *   checkInterval: 30 * 1000,
 *
 *   // Retry failed requests up to 5 times instead of default 3
 *   retryAttempts: 5,
 *
 *   // Wait 2 seconds between retries instead of default 1 second
 *   retryDelay: 2000
 * };
 *
 * const auth = await TonkAuth(options);
 * ```
 */
export type TonkAuthOptions = {
  /**
   * Callback function executed when user's subscription becomes inactive
   *
   * This is called during periodic checks if the subscription status
   * changes from active to inactive. Useful for displaying warnings
   * or gracefully shutting down CLI tools.
   *
   * @type {() => void}
   * @optional
   * @default undefined
   */
  onSubscriptionDisabled?: () => void;

  /**
   * Interval for periodic subscription status checks (in milliseconds)
   *
   * The client will automatically check subscription status at this interval
   * to ensure the user hasn't downgraded or cancelled their subscription.
   *
   * @type {number}
   * @optional
   * @default 60000 (1 minute)
   */
  checkInterval?: number;

  /**
   * Maximum number of retry attempts for failed API requests
   *
   * When subscription checks or authentication requests fail due to network
   * issues, the client will retry up to this many times before giving up.
   *
   * @type {number}
   * @optional
   * @default 3
   */
  retryAttempts?: number;

  /**
   * Base delay between retry attempts (in milliseconds)
   *
   * Each retry will wait: `retryDelay * attemptNumber` milliseconds.
   * For example, with retryDelay=1000: 1st retry waits 1s, 2nd waits 2s, etc.
   *
   * @type {number}
   * @optional
   * @default 1000 (1 second)
   */
  retryDelay?: number;
};

class _TonkAuth implements TonkAuthClient {
  private _activeSubscription: boolean = false;
  private _options: Required<TonkAuthOptions>;
  private _checkIntervalId: NodeJS.Timeout | null = null;
  private _subscriptionData: CliTokenPayload | null = null;
  __initialized: boolean = false;

  constructor(options: TonkAuthOptions = {}) {
    this._options = {
      onSubscriptionDisabled: options.onSubscriptionDisabled || (() => {}),
      checkInterval: options.checkInterval || 60 * 1000, // 1 minute
      retryAttempts: options.retryAttempts || 3,
      retryDelay: options.retryDelay || 1000,
    };

    this._initialize();
  }

  private async _initialize(): Promise<void> {
    try {
      // Check if user is already logged in
      if (clerkClient.session) {
        await this._updateSubscriptionStatus();
      }

      // Set up periodic checks
      this._setupPeriodicCheck();
      this.__initialized = true;
    } catch (error) {
      console.error("[TonkAuth] initialization failed:", error);
      throw new Error("TonkAuth initialization failed");
    }
  }

  private _setupPeriodicCheck(): void {
    if (this._checkIntervalId) {
      clearInterval(this._checkIntervalId);
    }

    this._checkIntervalId = setInterval(async () => {
      if (clerkClient.session) {
        await this._updateSubscriptionStatus();
      }
    }, this._options.checkInterval);
  }

  private async _updateSubscriptionStatus(): Promise<void> {
    const cache = await createSubscriptionTokenCache("tonk-cli");
    const token = cache.getToken();

    if (token) {
      const status = JSON.parse(token) as SubscriptionStatus;
      // check if active and not expired
      if (
        status.active &&
        !(status.data?.exp && status.data.exp < Date.now() / 1000)
      ) {
        this._activeSubscription = status.active;
        this._subscriptionData = status.data;
        return;
      }
    }
    await cache.clearToken();
    try {
      // fetch subscription token
      const result = await this._fetchSubscriptionTokenWithRetry();

      if (result.ok) {
        const wasActive = this._activeSubscription;
        this._activeSubscription = result.value.active;
        this._subscriptionData = result.value.data;
        await cache.clearToken();
        if (result.value.data) {
          await cache.saveToken(JSON.stringify(result.value));
        }

        // Call callback if subscription became inactive
        if (wasActive && !this._activeSubscription) {
          this._options.onSubscriptionDisabled();
        }
      } else {
        // Gracefully handle errors - don't update status on connection issues
        console.warn(
          "Failed to update subscription status:",
          result.error.message,
          `URL[${TONK_BASE_URL}]`,
        );
      }
    } catch (error) {
      console.warn("Error updating subscription status:", error);
    }
  }

  private async _fetchSubscriptionTokenWithRetry(): Promise<
    Result<SubscriptionStatus, Error>
  > {
    for (let attempt = 1; attempt <= this._options.retryAttempts; attempt++) {
      try {
        const result = await this._fetchSubscriptionToken();
        if (result.ok) {
          return result;
        }

        // Don't retry on authentication errors
        if (
          result.error.message.includes("No active Clerk session") ||
          result.error.message.includes("Failed to obtain session token")
        ) {
          return result;
        }

        // Don't retry on subscription inactive (402)
        if (result.error.message.includes("Subscription inactive")) {
          return {
            ok: true,
            value: {
              active: false,
              data: null,
            },
          };
        }

        // Retry on connection/server errors
        if (attempt < this._options.retryAttempts) {
          await delay(this._options.retryDelay * attempt);
          continue;
        }

        return result;
      } catch (error) {
        if (attempt === this._options.retryAttempts) {
          return err(error as Error);
        }
        await delay(this._options.retryDelay * attempt);
      }
    }

    return err(new Error("Max retry attempts exceeded"));
  }

  private async _fetchSubscriptionToken(): Promise<
    Result<SubscriptionStatus, Error>
  > {
    try {
      const clerk = clerkClient;

      if (!clerk.session) {
        return err(new Error("No active Clerk session"));
      }

      const session = await clerk.session.getToken();
      if (!session) {
        return err(new Error("Failed to obtain session token"));
      }

      const res = await fetch(TOKEN_SUBSCRIPTION_URL, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${session}`,
        },
      });

      if (!res.ok) {
        return err(new Error(`Token endpoint error ${res.status}`));
      }

      const responseData = (await res.json()) as {
        token?: string;
        status?: number;
        code?: string;
      };

      // Check if subscription is inactive (API returns { status: 402, code: "INACTIVE" })

      if (responseData.status === 402) {
        return ok({ active: false, data: null });
      }

      if (!responseData.token) {
        return err(new Error("No token in response"));
      }

      const token = responseData.token;
      const verifyResult = await this._verifyCliToken(token);

      if (!verifyResult.ok) {
        return err(verifyResult.error);
      }

      const cache = await createTokenCache("tonk-cli");
      await cache.saveToken(token);

      return ok(verifyResult.value);
    } catch (error) {
      return err(error as Error);
    }
  }

  private async _verifyCliToken(
    raw: string,
  ): Promise<Result<SubscriptionStatus, Error>> {
    try {
      const { payload } = await jwtVerify<CliTokenPayload>(raw, publicKey, {
        audience: "tonk-cli",
        issuer: [EXPECTED_ISSUER],
      });

      if (!payload.subscription_active) {
        return ok({ active: false, data: null });
      }

      return ok({ active: true, data: payload });
    } catch (error) {
      return err(error as Error);
    }
  }

  // Public API

  /**
   * Get the current subscription status
   */
  get activeSubscription(): boolean {
    return this._activeSubscription;
  }

  get isSignedIn(): boolean {
    return clerkClient.isSignedIn;
  }

  get email(): string {
    return this.__user?.primaryEmailAddress?.emailAddress ?? "unknown";
  }

  get friendlyName(): string | null {
    if (!this.__user) return null;
    return (
      this.__user?.firstName ??
      this.__user?.lastName ??
      this.__user?.username ??
      this.email
    );
  }

  get version(): string {
    return version;
  }

  get __user(): UserResource | null {
    return clerkClient.user ?? null;
  }

  get __session(): SessionResource | null {
    return clerkClient.session ?? null;
  }

  /**
   * Login user and automatically check subscription status
   */
  async login(): Promise<
    Result<{ data: (typeof clerkClient)["user"]; subscription: boolean }, Error>
  > {
    let localhostServer: Awaited<
      ReturnType<typeof createLocalhostAuthServer>
    > | null = null;

    try {
      localhostServer = await createLocalhostAuthServer();
      const url = encodeURI(
        `${TONK_AUTH_URL}?redirectUrl=${localhostServer.LOCALHOST_REDIRECT_URL}`,
      );

      await open(url);
      const token = await localhostServer.listenForAuthRedirect();

      const res = (await clerkClient.client?.signIn.create({
        strategy: "ticket",
        ticket: token,
      })) as SignInResource | undefined;

      if (!res) {
        return err(new Error("Sign-in request did not return a response"));
      }

      if (res.status !== "complete") {
        return err(new Error(`Flow did not complete: ${JSON.stringify(res)}`));
      }

      // Mark the newly created session as active so that the current
      // Clerk instance immediately reflects the authenticated state.
      if (res.createdSessionId) {
        await clerkClient.setActive({ session: res.createdSessionId });
      }

      // Ensure in-memory resources are refreshed
      await clerkClient.session?.reload();
      await clerkClient.client?.reload();

      // Immediately check subscription status after login
      await this._updateSubscriptionStatus();

      return ok({
        data: clerkClient.user,
        subscription: this._activeSubscription,
      });
    } catch (error) {
      return err(error as Error);
    } finally {
      // Always cleanup the server
      if (localhostServer) {
        localhostServer.cleanup();
      }
    }
  }

  /**
   * Logout user and cleanup
   */
  async logout(): Promise<Result<string, Error>> {
    try {
      clerkClient.client?.clearCache();

      if (clerkClient.session) {
        await clerkClient.signOut();
      }

      // Clear any locally stored auth data
      await clearStoredAuth();

      // Reset subscription status
      this._activeSubscription = false;
      this._subscriptionData = null;

      return ok("Logged out successfully");
    } catch (error) {
      return err(error as Error);
    }
  }

  /**
   * Cleanup resources and stop periodic checks
   */
  destroy(): void {
    if (this._checkIntervalId) {
      clearInterval(this._checkIntervalId);
      this._checkIntervalId = null;
    }
  }
}

/**
 * `tonk-auth` Client Interface
 *
 * Provides authentication and subscription management for the Tonk CLI.
 * This is the main interface returned by the TonkAuth factory function.
 *
 * @interface TonkAuthClient
 * @example
 * ```typescript
 * import { TonkAuth } from 'tonk-auth';
 *
 * const auth = await TonkAuth({
 *   onSubscriptionDisabled: () => console.log('Subscription expired!'),
 *   checkInterval: 60000 // Check every minute
 * });
 *
 * // Check authentication status
 * if (!auth.isSignedIn) {
 *   const result = await auth.login();
 *   if (result.ok) {
 *     console.log(`Welcome ${auth.friendlyName}!`);
 *     console.log(`Subscription active: ${auth.activeSubscription}`);
 *   }
 * }
 *
 * // Clean up when done
 * auth.destroy();
 * ```
 */
export interface TonkAuthClient {
  // Authentication State

  /**
   * Whether the user is currently signed in to Clerk
   * @readonly
   * @type {boolean}
   */
  readonly isSignedIn: boolean;

  /**
   * Whether the user has an active subscription
   * This is checked periodically and cached for performance
   * @readonly
   * @type {boolean}
   */
  readonly activeSubscription: boolean;

  // User Information

  /**
   * User's primary email address
   * Returns "unknown" if not available
   * @readonly
   * @type {string}
   */
  readonly email: string;

  /**
   * User's display name for friendly greeting
   * Fallback order: firstName ‚Üí lastName ‚Üí username ‚Üí email
   * Returns null when user is not authenticated
   * @readonly
   * @type {string | null}
   */
  readonly friendlyName: string | null;

  /**
   * Current library version from package.json
   * @readonly
   * @type {string}
   */
  readonly version: string;

  // Advanced Access (for debugging)

  /**
   * Direct access to Clerk user resource
   * ‚ö†Ô∏è Advanced usage only - for debugging or direct Clerk API access
   * @readonly
   * @type {UserResource | null}
   */
  readonly __user: UserResource | null;

  /**
   * Direct access to Clerk session resource
   * ‚ö†Ô∏è Advanced usage only - for debugging or direct Clerk API access
   * @readonly
   * @type {SessionResource | null}
   */
  readonly __session: SessionResource | null;

  // Methods

  /**
   * Authenticate user via browser and check subscription status
   *
   * Opens a browser window for OAuth authentication, then automatically
   * checks the user's subscription status and caches the result.
   *
   * @returns {Promise<Result<{data: UserResource | null | undefined, subscription: boolean}, Error>>}
   *   Result containing user data and subscription status, or error details
   *
   * @example
   * ```typescript
   * const result = await auth.login();
   * if (result.ok) {
   *   console.log(`Logged in as: ${result.value.data?.firstName}`);
   *   console.log(`Has subscription: ${result.value.subscription}`);
   * } else {
   *   console.error('Login failed:', result.error.message);
   * }
   * ```
   */
  login(): Promise<
    Result<
      { data: UserResource | null | undefined; subscription: boolean },
      Error
    >
  >;

  /**
   * Sign out user and clear all stored authentication data
   *
   * Clears Clerk session, local token cache, and resets subscription status.
   * This is a complete cleanup that ensures no authentication data remains.
   *
   * @returns {Promise<Result<string, Error>>} Success message or error details
   *
   * @example
   * ```typescript
   * const result = await auth.logout();
   * if (result.ok) {
   *   console.log(result.value); // "Logged out successfully"
   * } else {
   *   console.error('Logout failed:', result.error.message);
   * }
   * ```
   */
  logout(): Promise<Result<string, Error>>;

  /**
   * Clean up resources and stop periodic subscription checks
   *
   * Call this when you're done with the TonkAuth instance to prevent
   * memory leaks from the periodic subscription check interval.
   * This does NOT log out the user.
   *
   * @returns {void}
   *
   * @example
   * ```typescript
   * // Clean up when your app shuts down
   * process.on('SIGINT', () => {
   *   auth.destroy();
   *   process.exit(0);
   * });
   * ```
   */
  destroy(): void;
}

/**
 * Create a new TonkAuth client instance
 *
 * Factory function that creates and initializes a TonkAuth client for handling
 * CLI authentication and subscription management. The client automatically
 * handles token caching, periodic subscription checks, and provides a clean
 * API for login/logout operations.
 *
 * @async
 * @function TonkAuth
 * @param {TonkAuthOptions} [options={}] - Configuration options for the client
 * @returns {Promise<TonkAuthClient>} Promise that resolves to an initialized TonkAuth client
 * @throws {Error} Throws if client initialization fails
 *
 * @example
 * ```typescript
 * import { TonkAuth } from 'tonk-auth';
 *
 * // Basic usage with default options
 * const auth = await TonkAuth();
 *
 * // Advanced configuration
 * const auth = await TonkAuth({
 *   // Handle subscription expiration gracefully
 *   onSubscriptionDisabled: () => {
 *     console.log('‚ö†Ô∏è Subscription expired! Please upgrade to continue.');
 *     process.exit(1);
 *   },
 *
 *   // Check subscription status every 30 seconds
 *   checkInterval: 30 * 1000,
 *
 *   // Be more aggressive with retries
 *   retryAttempts: 5,
 *   retryDelay: 500
 * });
 *
 * // Use the client
 * if (!auth.isSignedIn) {
 *   const result = await auth.login();
 *   if (result.ok) {
 *     console.log(`Welcome back, ${auth.friendlyName}!`);
 *   }
 * }
 *
 * // Clean up when done (important for CLI apps)
 * process.on('SIGINT', () => {
 *   auth.destroy();
 *   process.exit(0);
 * });
 * ```
 *
 * @example
 * ```typescript
 * // Error handling
 * try {
 *   const auth = await TonkAuth();
 *
 *   // Check if user has valid subscription
 *   if (!auth.activeSubscription) {
 *     console.log('No active subscription found');
 *     const loginResult = await auth.login();
 *
 *     if (loginResult.ok && !loginResult.value.subscription) {
 *       console.log('Please upgrade your subscription to use this feature');
 *       return;
 *     }
 *   }
 *
 *   console.log(`‚úÖ Ready to go, ${auth.friendlyName}!`);
 * } catch (error) {
 *   console.error('Failed to initialize TonkAuth:', error);
 *   process.exit(1);
 * }
 * ```
 *
 * @since 1.0.0
 */
export async function TonkAuth(
  options: TonkAuthOptions = {},
): Promise<TonkAuthClient> {
  const auth = new _TonkAuth(options);
  while (!auth.__initialized) {
    await new Promise((resolve) => setTimeout(resolve, 1));
  }
  return auth;
}
