import { createServer, type Server } from "node:http";

const PORT = 1337;
const LOCALHOST_REDIRECT_URL: string = `http://127.0.0.1:${PORT}`;

const successHtml = /* html */ `
<body>
  <main style="height:100%;width:100%;display:flex;justify-content:center;align-items:center;font-family:ui-sans-serif,system-ui,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;">
    <div>
      <h2>Signed in</h2>
      <h3 style="font-weight: 400;">You can safely close this tab and return to your terminal</h3>
    </div>
  </main>
</body>
`;

export const createLocalhostAuthServer = async (): Promise<{
  LOCALHOST_REDIRECT_URL: string;
  listenForAuthRedirect: () => Promise<string>;
  cleanup: () => void;
}> => {
  let currentServer: Server | null = null;

  const listenForAuthRedirect = (): Promise<string> => {
    return new Promise<string>((resolve, reject) => {
      const server = createServer((req, res) => {
        const url = new URL(req.url || "", LOCALHOST_REDIRECT_URL);
        const token = url.searchParams.get("token") ?? "";

        // Send the success page back to the browser
        res.writeHead(200, { "Content-Type": "text/html" });
        res.end(successHtml);

        queueMicrotask(() => {
          resolve(token);
          server.close(() => {
            currentServer = null;
          });
        });
      });

      currentServer = server;

      server.on("error", (err: NodeJS.ErrnoException) => {
        if (err.code === "EADDRINUSE") {
          // Port is in use, reject immediately to allow retry logic
          reject(new Error(`Port ${PORT} is already in use`));
        } else {
          reject(err);
        }
      });

      server.listen(PORT);

      const close = () => {
        if (currentServer) {
          currentServer.close(() => {
            currentServer = null;
          });
        }
      };

      process.once("SIGINT", close);
      process.once("SIGTERM", close);
    });
  };

  const cleanup = () => {
    if (currentServer) {
      currentServer.close(() => {
        currentServer = null;
      });
    }
  };

  return { LOCALHOST_REDIRECT_URL, listenForAuthRedirect, cleanup };
};
