import pkg from "@clerk/clerk-js/headless/index.js";

const { Clerk } = pkg;

import { createTokenCache } from "./tokenCache";

/**
 * @dev Clerk Auth CLI based on https://github.com/clerk/cli-auth-unofficial-example
 */

export const SERVICE_PREFIX = "tonk-cli";

if (!process.env.CLERK_PUBLISHABLE_KEY) {
  throw new Error("CLERK_PUBLISHABLE_KEY environment variable not set");
}

const PUBLISHABLE_KEY = process.env.CLERK_PUBLISHABLE_KEY;

let clerkInstance: InstanceType<typeof Clerk> = null!;

const createClerkClient = async (
  binName = "tonk-cli",
): Promise<InstanceType<typeof Clerk>> => {
  const serviceName = `${SERVICE_PREFIX}-${binName}`;
  const tokenCache = await createTokenCache(serviceName);

  if (clerkInstance) return clerkInstance;

  clerkInstance = new Clerk(PUBLISHABLE_KEY);

  clerkInstance.__unstable__onBeforeRequest((req) => {
    req.credentials = "omit";
    req.url?.searchParams.append("_is_native", "1");
    (req.headers as Headers).set("authorization", tokenCache.getToken() || "");
  });

  clerkInstance.__unstable__onAfterResponse((_, resp) => {
    const auth = resp?.headers.get("authorization");
    if (auth) void tokenCache.saveToken(auth);
  });

  await clerkInstance.load({ standardBrowser: false });
  return clerkInstance;
};

await createClerkClient();

const clearClerkInstance = async (): Promise<void> => {
  clerkInstance = await createClerkClient();
};

export { clerkInstance as clerkClient, clearClerkInstance };
