import keytar from "keytar";
import { clearClerkInstance, SERVICE_PREFIX } from "./clerkClient";

export async function createTokenCache(serviceName: string): Promise<{
  getToken: () => string | undefined;
  saveToken: (token: string) => Promise<void>;
}> {
  let inMemory: string | null = await keytar.getPassword(
    serviceName,
    "__clerk_client_jwt",
  );

  return {
    getToken: () => inMemory ?? undefined,
    saveToken: async (token: string) => {
      inMemory = token;
      await keytar.setPassword(serviceName, "__clerk_client_jwt", token);
    },
  };
}

export async function createSubscriptionTokenCache(
  serviceName: string,
): Promise<{
  getToken: () => string | undefined;
  saveToken: (token: string) => Promise<void>;
  clearToken: () => Promise<void>;
}> {
  let inMemory: string | null = await keytar.getPassword(
    serviceName,
    "__subscription_token",
  );

  return {
    getToken: () => inMemory ?? undefined,
    saveToken: async (token: string) => {
      inMemory = token;
      await keytar.setPassword(serviceName, "__subscription_token", token);
    },
    clearToken: async () => {
      inMemory = null;
      try {
        await keytar.deletePassword(serviceName, "__subscription_token");
      } catch (error) {
        // Token might not exist, ignore error
        error as Error;
      }
    },
  };
}

export const clearStoredAuth = async (binName = "tonk-cli"): Promise<void> => {
  const serviceName = `${SERVICE_PREFIX}-${binName}`;
  try {
    await keytar.deletePassword(serviceName, "__clerk_client_jwt");
    await keytar.deletePassword(serviceName, "__subscription_token");
  } catch (error) {
    error as Error;
  }

  // Also clear subscription token
  try {
    await keytar.deletePassword(serviceName, "__subscription_token");
  } catch (error) {
    // Token might not exist, ignore error
    error as Error;
  }

  clearClerkInstance();
};
